import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

function Particle() {

   
        const particlesInit = async (main) => {
            console.log(main);
            await loadFull(main);
        };
    
        const particlesLoaded = (container)=> {
            console.log(container);
        };
    return(
        <Particles
        id="tsparticles"
        init={particlesInit}
        loaded={particlesLoaded}
        options={{
            
            fpsLimit: 120,
            interactivity: {
                events: {
                    onClick: {
                        enable: true,
                        mode: "push",
                    },
                    onHover: {
                        enable: true,
                        mode: "repulse",
                    },
                    resize: true,
                },
                modes: {
                    push: {
                        quantity: 4,
                    },
                    repulse: {
                        distance: 180,
                        duration: 0.4,
                    },
                },
            },
            particles: {
                color: {
                    value: "#411414",
                },
                links: {
                    color: "#000000",
                    distance: 130,
                    enable: true,
                    opacity: 0.5,
                    width: 3,
                },
                collisions: {
                    enable: true,
                },
                move: {
                    directions: "none",
                    enable: true,
                    outModes: {
                        default: "bounce",
                    },
                    random: false,
                    speed: 4,
                    straight: false,
                },
                number: {
                    density: {
                        enable: true,
                        area: 800,
                    },
                    value: 30,
                },
                opacity: {
                    value: 0.1,
                },
                shape: {
                    type: "circle",
                },
                size: {
                    value: { min: 1, max: 5 },
                },
            },
            detectRetina: true,
        }}
        />
    )
}

export default Particle